.content {
    &__info {
        padding: 10px 0;
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid grey;
        margin: 0 0 10px 0;

        div {
            text-align: center;
            flex: 1 1 33.333%;
            font-size: 18px;
        }
    }

    &__row {
        display: flex;
        flex-direction: column;
    }

    &__functionalBlock {
        display: flex;
        margin: 0 0 10px 0;
        gap: 10px;

        button {
            flex: 1 1 50%;
        }
    }

    .content__btn-icon {
        transition: 0.3s;
    }

    &__btn:hover {
        .content__btn-icon {
            transform: rotate(90deg);
        }
    }

    &__chart {
        margin: 0 0 10px 0;
    }

}